@import "bootstrap";
@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:300,400,600);

$blue: #1668af;
$light-blue: #239edd;
$light-green: #239edd;
$green: #119947;
$lime: #96c438;
$light-gray: #bcbec0;
$dark-gray: #6d6e71;

body{
	font-family: 'Source Sans Pro', sans-serif;
	font-size: 15px;

	a{
		text-decoration: none;

		&:hover{
			text-decoration: none;
			color: #000;
		}
	}

	ul{
		padding: 0;
		margin: 0;
		list-style: none;
	}
	

	header{
		text-align: right;

		.search{
	        background-color: #e9e9ea;
		    position: relative;
		    display: inline-block;
		    padding: 0 0 0 10px;
		    color: #808285;
			
			input{
				background-color: #e9e9ea;
				border: none;
				padding: 3px;

				&:focus{
					outline: none;
				}
			}

			select{
			    background-color: #dfdfe0;
			    border: none;
			    height: 100%;
			    display: inline-block;
			    background: url('../imgs/pt.png') no-repeat;
			    background-position: left;
				padding-left: 20px;
			}
		}

		nav{
			.navbar-header{
				button{
				    background-color: #e9e9ea;
				    margin-right: 0;

				    .icon-bar{
				    	background-color: #fff;
				    }
				}

				h1{
				    display: block;
				    margin: 0;

				    a{
		    	        height: auto;
					    display: block;
					    padding: 0;
					    float: none;

					    img{
					    	margin: 0 0 20px 0;
					    }
				    }
				}
			}

			.navbar-collapse{
				padding-right: 0;
			}

			ul{
				span{
				    display: inline-block;
				    width: 5px;
				    height: 5px;
				    background-color: #000;
				    border-radius: 50%;

				    &.blue{
				    	background-color: $blue;
				    }

				    &.l-blue{
				    	background-color: $light-blue;
				    }

				    &.l-green{
				    	background-color: $light-green;
				    }

				    &.green{
				    	background-color: $green;
				    }

				    &.lime{
				    	background-color: $lime;
				    }
				}

				li{
					display: inline-block !important;
					margin: 40px 22px;
					text-transform: uppercase;

					&:last-child{
						margin-right: 0;
					}

					a{
						color: #58595b;
						padding: 0;
					}

					&.active{
						a{
							border-bottom: 1px solid #239edd;
						}
					}
				}
			}
		}

		.cabecalho{
			background: url(../imgs/bg-header.jpg) no-repeat;
			background-size: cover;
			padding: 25px;
			text-align: left;

			h2{
		        color: #fff;
			    background: url(../imgs/airplane.png) no-repeat;
			    background-position: center left;
			    padding: 15px 0 15px 70px;
			    display: inline-block;
			    margin: 0;
			}

			.breadcrumbs{
		        padding: 0;
			    margin: 0;
			    list-style: none;
			    display: block;
			    float: right;
			    line-height: 4;

				li{
					display: inline-block;

					a{
						color: #fff;
					}
				}

    			li + li{
    				&:before{
    					padding: 0 5px;
					    color: #fff;
					    content: "/\00a0";
    				}
    			}
			}
		}
	}

	main{
		padding: 50px 0;

		.intro-text{
		    margin: 0 0 30px 0;
		}

		.topics{
			text-align: center;
			
			.sangria{
				margin-bottom: 30px;

				img{
					width: 100%;
				}

				.box{
					position: relative;
					display: inline-block;
					padding: 30px;
					color: #fff;
					width: 100%;
					text-align: left;
					height: 330px;
					
					.mask{
					    width: 100%;
					    height: 100%;
					    position: absolute;
					    top: 0;
					    left: 0;
					    overflow: hidden;

						img{
							height: 100%;
							opacity: 0.5;
						}

						&.purple{
							background-color: #2a166b;	
						}

						&.blue{
							background-color: $light-blue;
						}

						&.green{
							background-color: $lime;
						}
					}

					.content{
						position: relative;

						h3{
							font-weight: 300;
							margin: 0 0 20px 0;
						}

						ul{
							margin-bottom: 30px;
							font-weight: 300;

							li{
								.fz23{
									font-size: 23px;
								}
							}
						}
					}
				}
			}
		}
	}
	footer{
		.footer-above{
			background: url('../imgs/bg-footer.png') no-repeat;
			background-size: cover;
			padding: 40px 0;

			.container{
				position: relative;

				.f-title{
					margin: 0 0 30px 0;

					h3{
					    color: $dark-gray;
					    border-bottom: 2px solid $dark-gray;
					    display: inline-block;
					    padding-bottom: 5px;
					    margin: 0;
					}
				}

				ul{
					&.listagem{
						display: inline-block;
						vertical-align: top;

						&:last-child{
							margin-left: 55px;
						}
					}

					.btn-social{
						display: inline-block;
					    border: 2px solid #908f8d;
					    border-radius: 50%;
					    text-align: center;
					    font-size: 20px;
					    padding: 10px;

					    i{
					    	color: #908f8d;
					    }
					}
				}

				.number{
					font-size: 22px;
				}

				.anchor-top{
					i{
					    position: absolute;
						top: -20px;
						right: 0px;
						font-size: 15px;
						color: #bcbec0;
						border: 2px solid #bcbec0;
						padding: 5px;
						border-radius: 50%;
					}
				}

				a, p{
					color: $light-gray;
				}
			}
		}

		.footer-below{
			background-color: #231f20;
			color: #7d7d7d;
			padding: 10px 0;
			font-style: italic;
		}
	}
}

@media screen and (max-width: 768px) {
	body{
		header{
			text-align: center !important;

			nav{
				.navbar-header{
					padding-top: 20px;

					h1{
						img{
							width: 100px;
						}
					}
				} 

				.navbar-collapse{
				    position: absolute;
				    background-color: rgb(255, 255, 255);
				    width: 100%;
				    padding: 0px 20px;
				    left: 0px;

					span{
						display: none;
					}

					li{
						text-align: right;
						display: block !important;
						margin: 10px 0;
					}
				}
			}

			.cabecalho{
				background-position: center;
			    padding: 25px 10px 15px;
				
				h2{
					display: block;
				}

				.breadcrumbs{
					float: left;
				}
			}
		}

		main{
			padding: 30px 0;

			.topics{
				.sangria{
					.box{
						height: 290px;	
					}
				}
			}
		}
		

		footer {
			.footer-above {
				.container{
					ul {
						&.listagem{
							display: block;

							&:last-child{
								margin: 0;
							}
						}
					}
				}
			}
		}
	}
}

@media screen and (max-width: 996px) {
	body {
		header {
			nav {
				ul {
					li{
						margin: 40px 4px;
					}
				}
			}
		}
		main{
			.topics{
				.sangria{
					.box{
						height: 360px;
						padding: 10px;

						.content{
							h3{
								font-size: 22px;
							}
						}
					}
				}
			}
		}
		footer{
			.footer-above{
				.container{
					ul{
						&.listagem{
							&:last-child{
								margin: 0;
							}
						}
					}
				}
			}
		}
	}
}